.sk-progress-done {
    background: linear-gradient( to bottom, #d6dce1, #f0f8ff);
    //box-shadow: 0 3px 3px -5px #707df2, 0 2px 5px #707df2;
    //background:  #f0f8ff;
    border-radius: 20px;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 0;
    opacity: 0;
    transition: 1s ease 2s;
    
  }